












export default {
	data() {
		return {

		};
	},
	methods: {
		formSubmit: function(e) {
			console.log(e)
			this.$api.get('/api/member/validateInviteCode', {
				code: e.detail.value.code
			}, function(res) {
				console.log(res)

				if (res.data.code != 200) {
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					})
				} else {
					uni.showToast({
						title: res.data.msg,
					})
					setTimeout(function() {
						uni.navigateTo({
							url: '../register?id=' + res.data.data.user_id
						})
					}, 1000)

				}

			})
		}
	}
}
